<template>
  <div class="wrap1 ww100 pdt30 pdl20 pdr20 pdb30">

    <div>
      <p class="fs18 h28 lh28 cor0">科目管理</p>
      <div class="mgt30">
        <div class="ovh mgt20">
          <ul class="ulDiv fl" v-if="isAdminBud">
            <!--<li class="fl mgr30"><a href="###" class="db pdl29 corED145B ios2">新增科目</a></li>-->
            <router-link :to="{name:'subAdd'}" class="db pdl29 corED145B ios2">新增科目</router-link>
          </ul>
          <div class="fr">
            <form @submit.prevent="submit" class="form-inline">
              <div class="input-group">
                <input type="text" class="form-control" placeholder="科目名称/编号" v-model="params.searchKey">
                <span class="input-group-btn">
							<button type="submit" class="btn bged145b">
								<i class="glyphicon glyphicon-search corw"></i>
							</button>
						</span>
              </div>
            </form>
          </div>
        </div>


        <div class="mgt30">
          <table class="tab2">
            <tbody>
            <tr>
              <td>科目编号</td>
              <td>科目名称</td>
              <td>操作</td>
            </tr>
            <tr></tr>
            <tr v-for="(item,index) in subList">
              <td>{{ item.id }}</td>
              <td>{{ item.subName }}</td>
              <td>
                <!--<a href="###" class="btn btn-xs btn-success">查看</a>-->
                <!--<a href="###" class="btn btn-xs btn-primary">编辑</a>-->
                <a href="###" class="btn btn-xs btn-danger" @click.prevent="deleteSub(item.id)" v-if="isAdminBud">删除</a>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
      <!--分页-->
      <div class="fen c-b ovh mgt30 t-c w410">
        <page-vue :pageData="pageData" @chekdpageCallcak="chekdpage"></page-vue>
      </div>
      <!--分页END-->
    </div>

  </div>
</template>

<script>
    export default {
      name: "subList",
      data(){
          return {
            isAdminBud:true,
            params :{
              orgId: 1, pageNum: 1, pageSize: 10, searchKey: ''
            },
            subList:[],
            pageData: {
              curPage: 1,//当前页吗
              totalCount: 1,//总条数
              pageSize: 1//页面大小
            },
          }
      },

      created(){
        this.configJS.getUserInfo(this);
        initSubList(this);
        var  isAdminBud =  this.configJS.isAdminBud();
        this.isAdminBud = isAdminBud;
      },

      methods:{
        submit(){
          initSubList(this);
        },


        /******* 分页**********/
        chekdpage: function (pageNum) {
          this.params.pageNum = pageNum;
        },

        /**
         * 删除科目
         */
        deleteSub(subId){
          deleteSub(this,subId);
        }

      }
    }

    function deleteSub(that,subId) {
      that.$axios({
          url:"/biz/tob-budget/subject/deleteById",
          params:{
            subId:subId
          }
      }).then((res) => {
        console.log(res);
        if (res.data && res.data.code == 200){
          that.$message({
            type: 'success',
            message: '删除成功!',
            duration: 1000,
            onClose(){
              initSubList(that);
            }
          })
        }

      })
    }
    /**
     * 获取科目列表
     */
    function initSubList(that) {
      var params = that.params;
      params.orgId = that.configJS.userInfo.orgId;
      that.$axios({
        url:'/biz/tob-budget/subject/subList',
        params:params,
      }).then((res)=>{
        console.log(res);
        if (res.status == 200 && res.data.code == 200){
            var obj = res.data.obj;
            that.subList = obj.rows;
            that.pageData.totalCount = obj.total;//总条数
            that.pageData.curPage = that.params.pageNum;//当前页码
            that.pageData.pageSize = that.params.pageSize;//页面大小
        }
      }).catch((err)=>{
        console.log(err);
      })
    }


</script>

<style scoped>

</style>



